import axios from "axios";
import { useEffect } from "react";

const Socket = ({sendata}) => {
  useEffect(() => {
    // To kt ni HTTP n my ch ca bn
    const httpClient = axios.create({
      baseURL: `ws://123.24.199.156:18080/ws/vms-server/`,
      headers: {
        "Content-Type": "application/json",
      },
    });

    // Chuyn i kt ni HTTP thnh kt ni WebSocket
    let socket = new WebSocket(
      `ws://123.24.199.156:18080/ws/vms-server/`
    );
    // X l s kin khi kt ni WebSocket c m
    socket.onopen = () => {
      console.log("Kt ni  c m");
    };

    // X l s kin khi kt ni WebSocket nhn c tin nhn
    socket.onmessage = (event) => {
        const dataEvent = JSON.parse(event.data);
        console.log("dataEvent.event",dataEvent.event)
        if(dataEvent.event === "video_face_scan"){
          console.log("dataEvent.data",dataEvent.data)
          sendata(dataEvent.data)
        }
    };

    socket.onclose = () => {
      console.log("Socket mt kt ni . T ng kt ni li sau 1 giy");
      setTimeout(() => {
        socket = new WebSocket(
            `ws://123.24.199.156:18080/ws/vms-server/`
        );
      }, 1000);
    };
    // Gii phng kt ni WebSocket khi thnh phn b hu
    return () => {
      socket.close();
    };
  }, []);

  return <></>;
};
export default Socket;
